.root {
    padding: 0;
    margin: 0;
    width: 25%;
    display: flex;
    margin-left: -25%;
    transition: margin .5s;
    flex-direction: column;
    overflow-y: auto;
}

.open {
    margin-left: 0;
}

.wrap {
    padding: 5px;
}

.item {
    border-radius: 22px;
    padding: 10px 10px 10px 20px;
    margin-right: 10px;
    color: white;
    font-weight: bold;
    display: flex;
    // filter: opacity(0.6);
}

.work {
    background-color: coral;
}

.rest {
    background-color: darkcyan
}

.current {
    filter: none;
}

.passed {
    filter: opacity(0.6) brightness(0.2);
    > i {
      opacity: 0;
      pointer-events: none;
    }
}

.info {
    padding-left: 1rem;
    padding-right: 1rem;
    display: inline;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 100%;
}

.textArea {
    color: white;
    display: inline;
    width: 100%;
    border-bottom-color: white!important;
    box-shadow: none!important;
    padding: 0;
}