.root {
    width: 100%;
    padding-left: 1rem;
    padding-right: 1rem;
    transition: width .5s;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.work {
    background-color: coral;
}

.rest {
    background-color: darkcyan
}

.open {
    width: 75%;
}

.countdown {
    font-size: 200px;
    line-height: 1.2;
}
.info {
    font-size: 30px;
    text-align: center;
    line-height: 1.2;
}